body {
  font-family: "Roboto";
}

.App {
  text-align: center;
  min-height:90vh;
}

.list-item {
  text-align: center;
  list-style: none;
  margin-bottom: 0.5rem;
}

.title {
  font-weight: 400;
  font-size: 3rem;
  margin-top:0;
  margin-bottom: 0;
}

.submit-btn,
.completed-btn,
.not-completed-btn,
.edit-btn,
.delete-btn {
  height: 35px;
  width: 40px;
  outline: none;
  border: none;
  border-radius: 5px;
  margin-left: 1px;
  margin-right: 1px;
}

.completed,
.not-completed,
.input-form {
  height: 30px;
  border-radius: 5px;
}

.input-form {
  width: 75%;
}

.input-list {
  width: 55%;
}

.group-list {
  margin:auto
}

.completed,
.not-completed {
  border: none;
}

.completed {
  background-color: #93f0a9;
  text-decoration: line-through;
}

.not-completed {
  background-color: #8ee9f7;
}

.delete-btn {
  background-color: #dc3545;
  color: #fff
}

.edit-btn {
  background-color: #007bff;
  color: #fff
}

.not-completed-btn {
  background-color: #17a2b8;
  color: #fff
}

.completed-btn {
  background-color: #28a745;
  color: #fff
}

.css-signature {
  background:#f8f9fa;
  box-shadow: 0px -2px 5px 0px rgba(0,0,0,0.1);
  text-align: center;
}

.css-signature small {
  color : #181819
}

.fa-icones:hover {
  color: #ffc107;
  transition: 0.5s ease;
}

.fa-icones-linkedin:hover {
  color: #0270ad;
  transition: 0.5s ease;
}

.fa-icones-facebook:hover {
  color: #4867aa;
  transition: 0.5s ease;
}

.fa-icones-twitter:hover {
  color: #1da1f2;
  transition: 0.5s ease;
}

.fa-icones,
.fa-icones-linkedin,
.fa-icones-facebook, 
.fa-icones-twitter
{
  color: #181819;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
